<template>
<div class="new-question-container" v-if="currentView">
  <div class="file-container">
    <component :is="currentView.type" v-bind="currentView.props" :key="currentView.key"></component>
  </div>
  <div class="questions-container">
    <div class="question" v-for="question of questions" :key="question.key">
      <div class="question-body">
        <span class="label">{{question.seqId}}.</span><span>{{question.kindName}}</span><span>（{{question.score}}分）</span>
        <span>{{question.note}}</span>
      </div>
      <div v-if="question.kind === 2026">
        <template v-if="!question.hasReport">
          <span class="seq-label" v-for="option in question.options" :key="option.id" :class="option.multiAnswer?'seq-label-square':''">
            {{option.seqLabel}}
          </span>
        </template>
        <div class="q-stat" v-else>
          <div class="stat-chart">
            <bar-chart class="normal content"
              :options="question.options"
              :totalStuCount="totalStuCount"></bar-chart>
            <div class="overlay content"></div>
            <button class="ui primary button action content" @click="objResultModalTarget=question">查看详情</button>
          </div>
        </div>
      </div>
      <div v-if="question.kind !== 2026">
        <button class="ui basic massive icon button" v-if="curPage.status !== 3">
          <i class="ui photo icon"></i>
        </button>
        <div v-if="curPage.hasReport && totalStuCount">
          <inline-subjective-results
            :question="question"
            :all-students="allStudents"></inline-subjective-results>
        </div>
      </div>
    </div>
  </div>
  <modal-objective-results
    :question="objResultModalTarget"
    :allStudents="allStudents"
    v-if="showStat && objResultModalTarget && objResultModalTarget.hasReport"
    @close="objResultModalTarget=null"></modal-objective-results>
</div>
</template>

<script>
import axios from 'axios'
import pageDocument from 'components/doc/office'
import pageImage from 'components/doc/image'
import pageVideo from 'components/doc/video'
import pageAudio from 'components/doc/audio'
import { mapState, mapGetters } from 'vuex'
import { getQuestionByData } from 'assets/js/questions/view-mode'
import ModalObjectiveResults from 'components/questions/modal-objective-results'
import BarChart from 'components/questions/bar-chart'
import InlineSubjectiveResults from './questions/inline-subjective-results'

export default {
  name: 'page-new-question',
  props: {
    newQuestionId: { type: Number, required: true },
  },
  data() {
    return {
      nq: null,
      targetKind: 2025,
      questions: [],
      objResultModalTarget: null,
    }
  },
  created() {
    this.fetchNqData()
  },
  components: {
    'page-document': pageDocument,
    'page-image': pageImage,
    'page-video': pageVideo,
    'page-audio': pageAudio,
    'inline-subjective-results': InlineSubjectiveResults,
    ModalObjectiveResults,
    BarChart,
  },
  computed: {
    currentView() {
      if (!this.nq || !this.nq.fileUrl) {
        return null
      }
      let type = 'page-document'
      switch (this.nq.fileType) {
        case 2020:
        case 2024:
        case 2028:
        case 2021:
          type = 'page-document'; break
        case 2022:
          type = 'page-image'; break
        case 2004:
          type = 'page-video'; break
        case 2023:
          type = 'page-audio'; break
        default:
          return null
      }
      return {
        type,
        props: { fileUrl: this.nq.fileUrl },
        key: `${this.nq.kind}${this.nq.id}`,
      }
    },
    ...mapState(['curPage']),
    ...mapGetters(['totalStuCount', 'allStudents']),
  },
  watch: {
    curPage(val) {
      if (val.targetKind === 2025) {
        this.fetchNqData()
      }
    },
  },
  methods: {
    fetchNqData() {
      if (this.curPage.status === 3) {
        axios.all([
          axios.get('/pad/j/fetch/new_question', { params: { newQuestionId: this.newQuestionId } }),
          axios.get('/pad/j/coursematerial/report', { params: { courseMaterialId: this.curPage.cmId, subTargetKind: this.targetKind } }),
        ]).then(axios.spread((qResp, rResp) => {
          this.nq = qResp.data.nq
          this.questions = []
          const questionsLookUp = {}
          for (const questionData of this.nq.questions) {
            const q = getQuestionByData(questionData)
            questionsLookUp[q.key] = q
            this.questions.push(q)
          }

          for (const reportData of rResp.data.report) {
            const key = `key-${reportData.kind}-${reportData.seqId}`
            if (questionsLookUp.hasOwnProperty(key)) {
              questionsLookUp[key].injectReportData(reportData)
            }
          }
          this.$store.commit('pageReportAttached')
        }))
      } else {
        axios.get('/pad/j/fetch/new_question', { params: { newQuestionId: this.newQuestionId } })
          .then((response) => {
            this.nq = response.data.nq
            this.questions = []
            for (const questionData of this.nq.questions) {
              const q = getQuestionByData(questionData)
              this.questions.push(q)
            }
          })
      }
    },
  },
}
</script>

<style lang="less" scoped>
@import "~assets/css/base-variables.less";
.new-question-container {
  width: 100%;
  height: 100%;
  display: flex;
  .file-container {
    flex: 2;
    height: 100%;
    border-right: 1px #ccc solid;
    position: relative;
  }
  .questions-container {
    flex: 1;
    font-family: Palatino, Times, Arial, SimSun, "Microsoft YaHei", "微软雅黑", STHeiti, sans-serif;
    height: 100%;
    max-height: 100%;
    overflow-y: scroll;
    padding: 12px;
    &::-webkit-scrollbar {
      width: 0;
      background: transparent;
    }
    .seq-label {
      font-family: @base-font-family;
      display: inline-block;
      margin-right: .5em;
      width: 2em;
      line-height: 2em;
      text-align: center;
      border: solid 1px @color-gray;
      border-radius: 1em;
      &.seq-label-square {
        border-radius: 2px;
      }
    }
    .ui.button {
      font-family: initial;
      font-weight: bold;
    }
    .label {
      font-weight: bold;
    }
  }
  .question {
    font-size: 1.2em;
    .question-body {
      margin-bottom: .5em;
    }
    margin-bottom: 1.2em;
  }
}

.choice-chart{
  width: 100%;
  height: auto;
  font-family: initial;
  font-size: 14px;
  .progress-base {
    fill: @color-light-gray;
  }
  text {
    fill: @color-light-black;
    font-weight: bold;
  }
}
.choice-chart .correct .seq-label {
  fill: @color-white;
}

.correct {
  circle {
    fill: @color-teal;
  }
  .progress {
    fill: @color-teal;
  }
}
.wrong {
  circle {
    fill: @color-light-gray;
  }
  .progress {
    fill: @color-orange;
  }
}
</style>




