<template>
<div class="quesiton-slide-show" v-if="question">
  <div class="main-section">
    <span class="type-label">{{question.kindName}}</span>
    <span v-html="question.stemHtml" class="richtext"></span>
  </div>
  <blank-sub-panel :question="question" :has-sub-stem="false" :key="question.key"></blank-sub-panel>
</div>
</template>

<script>
import BlankSubPanel from './blank-sub-panel'

require('assets/css/richtext.less')
require('./base.less')

export default {
  name: 'question-blank',
  props: ['question'],
  data() {
    return {}
  },
  components: { BlankSubPanel },
}
</script>



