<template>
  <div class="ui primary buttons" v-if="questions.length > 0" v-cloak>
    <div class="ui icon button" :class="qIndex === 0 ? 'disabled' : ''" @click="updateQIndex(qIndex - 1)">
      <i class="left chevron icon"></i>
    </div>
    <div class="ui scrolling dropdown button">
      <i class="caret up icon"></i>{{qIndex+1}}. {{questions[qIndex].kindName}}
      <div class="menu">
        <div class="ui header large">
          大题列表
        </div>
        <div class="item" v-for="(q, idx) of questions" :key="''+q.kind+q.id" :data-value="idx" @click="updateQIndex(idx)">
            {{idx+1}}. {{q.kindName}}
        </div>
      </div>
    </div>
    <div class="ui icon button" :class="qIndex + 1 === questions.length ? 'disabled' : ''"
      @click="updateQIndex(qIndex + 1)">
      <i class="right chevron icon"></i>
    </div>
  </div>
</template>


<script>
import { mapState } from 'vuex'

export default {
  name: 'question-selector',
  data() {
    return {}
  },
  mounted() {
    $(this.$el).find('.ui.dropdown').dropdown({
      action: 'hide',
    })
    window.addEventListener('keyup', this.changeQIdx)
  },
  beforeDestroy() {
    $('.ui.dropdown').dropdown('destroy')
    window.removeEventListener('keyup', this.changeQIdx)
  },
  computed: mapState(['questions', 'qIndex']),
  methods: {
    updateQIndex(val) {
      this.$store.commit('updateQIndex', { qIndex: val })
    },
    changeQIdx(e) {
      if (e.keyCode === 37 && e.ctrlKey && this.qIndex > 0) {
        this.updateQIndex(this.qIndex - 1)
      } else if (e.keyCode === 39 && e.ctrlKey && this.qIndex < this.questions.length - 1) {
        this.updateQIndex(this.qIndex + 1)
      }
    },
  },
}
</script>

<style lang="less" scoped>
.ui.dropdown .menu>.header {
  font-size: 1em;
}
.ui.dropdown {
  width: 130px;
  text-align: center;
  padding-left: .3em;
  padding-right: .3em;
}
.ui.buttons {
  margin-right: 203x;
}
.ui.button {
  line-height: 1.857;
}
.ui.dropdown .scrolling.menu, .ui.scrolling.dropdown .menu {
  max-height: 30rem;
}
</style>



