<template>
<div class="quesiton-slide-show" v-if="question">
  <div class="main-section">
    <span class="type-label">{{question.kindName}}</span>
    <span v-if="!question.audio" class="label"> 缺少音频文件 </span>
    <template v-else>
      <audio controls controlsList="nodownload"><source :src="question.audio.fileUrl">您的浏览器版本过低，请更新</audio>
      <div class="ui accordion">
        <div class="title">
          <span class="accordion-title-label">查看原文<i class="dropdown icon"></i></span>
        </div>
        <div class="content">
          <p v-html="question.audio.textHtml"></p>
        </div>
      </div>
    </template>
  </div>
  <sub-question-panel
    :question="question"
    :key="question.key"></sub-question-panel>
</div>
</template>

<script>
import SubQuestionPanel from './sub-question-panel'

require('./base.less')
require('assets/css/richtext.less')

export default {
  name: 'question-long-dialog',
  props: ['question'],
  data() {
    return {}
  },
  components: {
    'sub-question-panel': SubQuestionPanel,
  },
  mounted() {
    $('.ui.accordion').accordion()
  },
  beforeDestroy() {
    $('.ui.accordion').accordion('destroy')
  },
}
</script>

<style lang="less" scoped>
audio {
  display: inline;
  vertical-align: middle;
}
</style>



