<template>
<div class="inline-subjective-result" v-cloak>
  <template v-if="question.results.length">
    <h3 class="ui header">已作答<span class="note"> 点击标签查看详情</span></h3>
    <student-labels
      :allStudents="allStudents"
      :orderedResults="question.results"
      @selected="showAnswerImage"></student-labels>
  </template>
  <template v-if="question.unansweredSids.length">
    <h3 class="ui header">提交未作答</h3>
    <student-labels
      :allStudents="allStudents"
      :orderedSids="question.unansweredSids"
      :active="false"></student-labels>
  </template>
  <template v-if="unsubmitedSids.length">
    <h3 class="ui header">未提交</h3>
    <student-labels
      :allStudents="allStudents"
      :orderedSids="unsubmitedSids"
      :active="false"></student-labels>
  </template>
  <div v-if="resultOnDisplay" class="images-container">
    <ul id="images">
      <li v-for="image of resultOnDisplay.images"><img :src="image.url" :alt="allStudents[resultOnDisplay.sid].name + '的答案'"></li>
    </ul>
  </div>
</div>
</template>


<script>
// model 形式查看学生的主观题作答
// 不区分是否批改，均归入 已作答
import _ from 'lodash'
import StudentLabels from 'components/student-labels'

require('assets/css/richtext.less')
const $script = require('scriptjs')

export default {
  name: 'inline-subjective-results',
  props: {
    question: {
      type: Object,
      required: true,
    },
    allStudents: {
      type: Object,
      required: true,
      default: null,
    },
  },
  data() {
    return {
      viewer: null,
      resultOnDisplay: null,
      viewStudentResult: null,
    }
  },
  components: { StudentLabels },
  computed: {
    unsubmitedSids() {
      let sids = _.map(Object.values(this.allStudents), 'id')
      sids = _.difference(sids, this.question.unansweredSids)
      sids = _.difference(sids, _.map(this.question.results, 'sid'))
      return sids
    },
  },
  mounted() {
  },
  beforeDestroy() {
    if (this.viewer) {
      this.viewer.destroy()
    }
  },
  methods: {
    showAnswerImage(result) {
      this.resultOnDisplay = result
      this.$nextTick(() => {
        if (!this.viewer) {
          $script('/static/w/assets/viewer/viewer.js', () => {
            this.viewer = new Viewer(document.getElementById('images'), {
              inline: false,
              button: true,
              navbar: true,
              title: true,
              toolbar: false,
              scalable: false,
              keyboard: false,
              playControl: false,
              transition: false,
            })
          })
        } else {
          this.viewer.update()
        }
        this.viewer.show()
      })
    },
  },
}
</script>
<style>
  @import "/static/w/assets/viewer/viewer.css";
</style>
<style lang="less">
.viewer-container {
  background-color: rgba(0, 0, 0, .8);
  .viewer-footer .viewer-title {
    display: block;
    font-size: 1.2rem;
    font-weight: bold;
  }
}
</style>

<style lang="less" scoped>
@import "~assets/css/base-variables.less";

.inline-subjective-result {
  font-family: initial;
}
.images-container {
  display: none;
}

.note {
  color: #888;
  font-size: .8em;
}
.ui.modal {
  max-height: 90vh;
  .content {
    max-height: 100%;
  }
}

.content .ui.image.label {
  font-size: 13px;
  justify-self: left;
  align-self: center;
  min-width: 8em;
  background-color: #f6f6f6;
  color: #333;
  &.carousel-button {
    cursor: pointer;
  }
}

.header.ui {
  border-left: .3rem solid @color-primary;
  padding-left: .5rem;
  font-size: 1.2rem;
  font-weight: normal;
  margin-top: 1rem;
}
[v-cloak] {
  display: none;
}
</style>



