import Vue from 'vue'
import Vuex from 'vuex'
import Raven from 'raven-js'
import RavenVue from 'raven-js/plugins/vue'
import Modal from 'components/modal'
import StudentLabels from 'components/student-labels'
import pageDocument from 'components/doc/office'
import pageImage from 'components/doc/image'
import pageVideo from 'components/doc/video'
import pageAudio from 'components/doc/audio'
import loader from 'components/loader'
import _ from 'lodash'
import pagePaper from './paper'
import pageNewQuestion from './new-question'
import questionSelector from './question-selector'

require('./index.less')
const qs = require('qs')

Vue.config.productionTip = false

const axios = require('axios')

axios.defaults.headers.common.X_REQUESTED_WITH = 'XMLHttpRequest'
axios.defaults.xsrfHeaderName = 'X-CSRFTOKEN'
axios.defaults.xsrfCookieName = 'csrftoken'

Vue.prototype.$raven = Raven
if (process.env.NODE_ENV === 'production') {
  Raven.config('http://216e3d581cd9424fad7f5d8c046b2f64@sentry.xdf100.com/6').addPlugin(RavenVue, Vue).install()
}

Storage.prototype.setObject = function (key, value) {
  this.setItem(key, JSON.stringify(value))
}

Storage.prototype.getObject = function (key) {
  const value = this.getItem(key)
  return value && JSON.parse(value)
}

const urlMatch = window.location.pathname.match(/\/pad\/coursewares\/(\d+)\/detail/)
const cwId = parseInt(urlMatch ? urlMatch[1] : 97)

Vue.use(Vuex)


const store = new Vuex.Store({
  state: {
    cwId,
    questions: [],
    qIndex: 0,
    subPanelMin: false,
    fontSize: parseInt(localStorage.getItem(`${document.cookie}-fontSize`)) || 18,
    curPage: null,
    pageSubmitted: [],
    pageUnsubmitted: [],
    pageViews: {},
  },
  mutations: {
    updateQuestions(state, payload) {
      state.questions = payload.questions
    },
    updateQIndex(state, payload) {
      state.qIndex = payload.qIndex
    },
    toggleSubPane(state) {
      state.subPanelMin = !state.subPanelMin
    },
    updateFontSize(state, payload) {
      state.fontSize += payload.increment
      localStorage.setItem(`${document.cookie}-fontSize`, state.fontSize)
    },
    updateCurpage(state, payload) {
      const curPage = payload.curPage
      state.curPage = curPage
      if (!state.pageViews.hasOwnProperty(payload.curPage.id)) {
        let view = { type: 'page-document', props: { fileUrl: curPage.docUrl } }
        switch (payload.curPage.targetKind) {
          case 2022:
            view = { type: 'page-image', props: { fileUrl: curPage.docUrl } }; break
          case 2004:
            view = { type: 'page-video', props: { fileUrl: curPage.docUrl } }; break
          case 2023:
            view = { type: 'page-audio', props: { fileUrl: curPage.docUrl } }; break
          case 2018:
            view = { type: 'page-paper', props: { paperId: curPage.targetId } }; break
          case 2025:
            view = { type: 'page-new-question', props: { newQuestionId: curPage.targetId } }; break
          default:
            break
        }
        view.id = payload.curPage.id
        state.pageViews[payload.curPage.id] = view
      }
    },
    setPageSubmitted(state, payload) {
      state.pageSubmitted = payload.pageSubmitted
    },
    setPageUnsubmitted(state, payload) {
      state.pageUnsubmitted = payload.pageUnsubmitted
    },
    pageReportAttached(state) {
      state.curPage.hasReport = true
    },
  },
  getters: {
    curQuestion: (state) => {
      if (state.questions.length > 0) {
        return state.questions[state.qIndex]
      }
      return null
    },
    curPageIsPaper: (state) => {
      if (!state.curPage) {
        return false
      }
      return state.curPage.targetKind === 2018
    },
    curPageIsNQ: (state) => {
      if (!state.curPage) {
        return false
      }
      return state.curPage.targetKind === 2025
    },
    allStudents: (state) => {
      const students = {}
      for (const s of state.pageSubmitted) {
        students[s.id] = s
      }
      for (const s of state.pageUnsubmitted) {
        students[s.id] = s
      }
      return students
    },
    totalStuCount: state => state.pageSubmitted.length + state.pageUnsubmitted.length,
    pageSubmitLabel: (state, getters) => {
      if (getters.curPageIsPaper || getters.curPageIsNQ) {
        return '提交'
      }
      return '查看'
    },
  },
})

new Vue({
  delimiters: ['${', '}'],
  el: '#app',
  store,
  data: {
    teachingCourseId: 0,
    cwName: '',
    preClass: {},
    inClass: {},
    afterClass: {},
    useFullScreen: false,
    loading: true,
    submitStatModalOpen: false,
  },
  components: {
    loader,
    'page-document': pageDocument,
    'page-image': pageImage,
    'page-video': pageVideo,
    'page-audio': pageAudio,
    'page-paper': pagePaper,
    'page-new-question': pageNewQuestion,
    'question-selector': questionSelector,
    Modal,
    StudentLabels,
  },
  mounted() {
    axios.get('/pad/j/teach/courseware', { params: { cwId: this.cwId } })
      .then((response) => {
        _.assign(this, response.data)
        const pages = _.concat(this.inClass.pages, this.preClass.pages, this.afterClass.pages)
        this.$store.commit('updateCurpage', { curPage: pages[0] })
        $('#cw-pages-shift').sidebar({ context: '.main-content', transition: 'overlay' })
        this.$nextTick(() => {
          this.loading = false
        })
      })
  },
  computed: {
    ...Vuex.mapState(['questions', 'cwId', 'qIndex', 'curPage', 'pageSubmitted', 'pageUnsubmitted', 'pageViews']),
    ...Vuex.mapGetters(['curPageIsPaper', 'curPageIsNQ', 'allStudents', 'pageSubmitLabel']),
  },
  watch: {
    curPage(val) {
      if (val && val.status !== 1) {
        this.updateSubmitInfo()
      }
    },
  },
  methods: {
    resizeFont(val) {
      this.$store.commit('updateFontSize', { increment: val })
    },
    updateSubmitInfo() {
      if (!this.curPage) return

      return axios.get('/pad/j/cm/students/submit', { params: { courseMaterialId: this.curPage.cmId } })
        .then((response) => {
          this.$store.commit('setPageSubmitted', { pageSubmitted: response.data.submitted })
          this.$store.commit('setPageUnsubmitted', { pageUnsubmitted: response.data.unsubmitted })
        })
    },
    showSubmitted() {
      this.updateSubmitInfo().then(this.submitStatModalOpen = true)
    },
    fetchCwData() {
      return axios.get('/pad/j/teach/courseware', { params: { cwId: this.cwId } })
        .then((response) => {
          _.assign(this, response.data)
          const pages = _.concat(this.inClass.pages, this.preClass.pages, this.afterClass.pages)
          for (const page of pages) {
            if (page.id === this.curPage.id) {
              this.$store.commit('updateCurpage', { curPage: page })
              break
            }
          }
        })
    },
    togglePagesPanel() {
      if ($('#cw-pages-shift').sidebar('is hidden')) {
        this.fetchCwData()
          .then(() => {
            $('#cw-pages-shift').sidebar('show')
          })
      } else {
        $('#cw-pages-shift').sidebar('hide')
      }
    },
    shiftToPage(page) {
      this.$store.commit('updateCurpage', { curPage: page })
      $('#cw-pages-shift').sidebar('hide')
    },
    sendCwPage(sendType, target, event) {
      const params = {
        sendType,
        target,
        cwId: this.cwId,
        courseId: this.teachingCourseId,
      }
      $(event.target).addClass('loading')
      axios.post('/pad/j/teach/send_courseware_page', qs.stringify(params)).then(resp => {
        $(event.target).removeClass('loading')
        this.fetchCwData()
      })
    },
    stopAnswering(page, event) {
      $(event.target).addClass('loading')
      axios.post('/pad/j/cm/stop_answering', qs.stringify({ courseMaterialId: page.cmId }))
        .then(() => { this.fetchCwData() })
    },
    toggleFullscreen() {
      if (!this.useFullScreen) {
        const element = document.documentElement
        const requestMethod = element.webkitRequestFullScreen || // Chrome等
          element.requestFullScreen || // W3C
          element.mozRequestFullScreen || // FireFox
          element.msRequestFullScreen // IE11
        requestMethod.call(element)
        this.useFullScreen = !this.useFullScreen
      } else {
        const exitMethod = document.mozCancelFullScreen || // Chrome等
          document.exitFullscreen || // W3C
          document.webkitExitFullscreen || // FireFox
          document.webkitExitFullscreen // IE11
        exitMethod.call(document)
        this.useFullScreen = !this.useFullScreen
      }
    },
  },
})



