<template>
<div class="quesiton-slide-show" v-if="question">
  <div class="main-section">
    <span class="type-label">{{question.kindName}}</span>
    <span v-html="question.stemHtml" class="richtext"></span>
    <template v-for="(subQ, qIdx) of question.subQuestions" v-if="subQ.stemHtml">
      <br>
      <span v-if="hasMultiSubQ" >({{qIdx + 1}})</span>
      <span v-html="subQ.stemHtml"></span>
    </template>
    <div class="ui accordion" v-show="question.hasReport">
      <div class="title">
        <span class="accordion-title-label">作答详情<i class="dropdown icon"></i></span>
      </div>
      <div class="content">
        <inline-subjective-results
          :question="question"
          :all-students="allStudents"
          v-if="question.hasReport"></inline-subjective-results>
      </div>
    </div>
    <div v-for="(subQuestion, qIdx) of question.subQuestions" :key="subQuestion.id">
      <div class="ui accordion">
        <div class="title">
          <span v-if="question.subQuestions.length > 1" class="accordion-title-label">小题 {{qIdx + 1}} / 解析<i class="dropdown icon"></i></span>
          <span v-else class="accordion-title-label">查看解析<i class="dropdown icon"></i></span>
        </div>
        <div class="content">
          <div v-for="answer of subQuestion.answers" :key="answer.id" v-html="answer.textHtml"></div>
          <template v-for="(solution, idx) of subQuestion.solutions">
            <div class="solution" :key="idx">
              <span v-if="subQuestion.solutions.length > 1">解法 {{idx + 1}}：</span>
              <span v-for="analysis of solution.analyses" :key="analysis.id" v-html="analysis.textHtml"></span>
              <p v-if="solution.textHtml" v-html="solution.textHtml"></p>
              <template v-if="solution.knowledges.length">
                <div class="tag-list">
                  <span class="item-title-label">考点</span>
                  <span class="ui label" v-for="knowledge of solution.knowledges" :key="knowledge.id">{{knowledge.name}}</span>
                </div>
              </template>
              <template v-if="solution.methods.length">
                <div class="tag-list">
                  <span class="item-title-label">解题方法</span>
                  <span class="ui label" v-for="method of solution.methods" :key="method.id">{{method.name}}</span>
                </div>
              </template>
            </div>
          </template>
        </div>
      </div>
    </div>
  </div>
</div>
</template>

<script>
import { mapState, mapGetters } from 'vuex'
import InlineSubjectiveResults from './inline-subjective-results'

require('assets/css/richtext.less')
require('./base.less')

export default {
  name: 'question-single-choice',
  props: ['question'],
  data() {
    return {}
  },
  mounted() {
    $('.ui.accordion').accordion()
    this.$nextTick(() => {
      MathJax.Hub.Queue(["Typeset", MathJax.Hub])
    })
  },
  components: {
    'inline-subjective-results': InlineSubjectiveResults,
  },
  beforeDestroy() {
    $('.ui.accordion').accordion('destroy')
  },
  computed: {
    ...mapState(['curPage']),
    ...mapGetters(['allStudents']),
  },
}
</script>



