<template>
<div class="quesiton-slide-show" v-if="question">
  <div class="main-section">
    <span class="type-label">{{question.kindName}}</span>
    <span v-html="question.stemHtml" class="richtext"></span>
    <div class="options">
      <div class="option" v-for="option in question.options" :key="option.id">
        <span class="seq-text">{{option.seqLabel}}. </span>
        <span v-html="option.optionHtml"></span>
      </div>
    </div>
    <div class="ui divider"></div>
    <div>
      <div class="q-stat" v-if="curPage.hasReport">
        <div class="stat-chart">
          <bar-chart class="normal content"
            :options="question.options"
            :totalStuCount="totalStuCount"
            v-if="question.hasReport"></bar-chart>
          <div class="overlay content"></div>
          <button class="ui primary button action content" @click="showStat=true">查看详情</button>
        </div>
        <modal-objective-results
          :question="question"
          :allStudents="allStudents"
          @close="showStat=false"
          v-if="showStat && question && question.hasReport"
          ></modal-objective-results>
      </div>
      <div class="ui accordion">
        <div class="title">
          <span class="accordion-title-label">查看解析<i class="dropdown icon"></i></span>
        </div>
        <div class="content">
          答案：{{question.answerLabel}}
          <p v-html="question.analysisHtml"></p>
        </div>
      </div>
    </div>
  </div>
</div>
</template>

<script>
import BarChart from 'components/questions/bar-chart'
import ModalObjectiveResults from 'components/questions/modal-objective-results'
import { mapState, mapGetters } from 'vuex'

require('assets/css/richtext.less')
require('./base.less')

export default {
  name: 'question-single-choice',
  props: ['question'],
  data() {
    return {
      showStat: false,
    }
  },
  components: { BarChart, ModalObjectiveResults },
  mounted() {
    $('.ui.accordion').accordion()
    if (this.curPage.status === 3) {
      $('.modal-answer-detail')
        .modal({
          observeChanges: true,
          transition: 'fade',
          duration: 300 }).modal('attach events', '.button-answer-detail', 'show')
    }
  },
  beforeDestroy() {
    $('.ui.accordion').accordion('destroy')
    $('.ui.modals.dimmer .modal-answer-detail').remove() // Todo: too hacky
  },
  computed: {
    stuWrongCount() {
      if (!this.question.hasReport) {
        return 0
      }
      let count = 0
      for (const option of this.question.options) {
        if (!option.isAnswer) {
          count += option.sids.length
        }
      }
      return count
    },
    stuCorrectCount() {
      if (!this.question.hasReport) {
        return 0
      }
      for (const option of this.question.options) {
        if (option.isAnswer) {
          return option.sids.length
        }
      }
    },
    ...mapState(['curPage']),
    ...mapGetters(['allStudents', 'totalStuCount']),
  },
}
</script>

<style lang="less" scoped>
.q-stat {
  float: right;
}
</style>



