<template>
<div class="quesiton-slide-show" v-if="question">
  <div class="main-section">
    <span class="type-label">{{question.kindName}}</span>
    <span v-html="question.stemHtml" class="richtext"></span>
  </div>
  <sub-question-panel :question="question" :has-sub-stem="false" :key="question.key"></sub-question-panel>
</div>
</template>

<script>
import SubQuestionPanel from './sub-question-panel'

require('assets/css/richtext.less')
require('./base.less')

export default {
  name: 'question-cloze',
  props: ['question'],
  data() {
    return {}
  },
  components: {
    'sub-question-panel': SubQuestionPanel,
  },
}
</script>



