<template>
<div class="question-container" v-if="questions.length" :style="'fontSize:' + fontSize + 'px'">
  <component :is="currentView" v-bind="{question: curQuestion}" :key="''+curQuestion.kind+curQuestion.id"></component>
</div>
</template>

<script>
import axios from 'axios'
import { mapState, mapGetters } from 'vuex'
import { getQuestionByData, QuestionKindToMV } from './questions/teach-mode'

const components = {}
for (const item of Object.values(QuestionKindToMV)) {
  components[item.viewName] = item.view
}

export default {
  name: 'page-paper',
  props: {
    paperId: { type: Number, required: true },
  },
  data() {
    return {
      targetKind: 2018,
    }
  },
  created() {
    this.fetchPaperData()
      .then(() => {
        this.$store.commit('updateQIndex', { qIndex: 0 })
      })
  },
  components,
  computed: {
    currentView() {
      return QuestionKindToMV[this.curQuestion.kind].view
    },
    ...mapState(['questions', 'qIndex', 'fontSize', 'curPage']),
    ...mapGetters(['curQuestion', 'allStudents']),
  },
  watch: {
    curPage(val) {
      if (val.targetKind === 2018) {
        this.fetchPaperData()
      }
    },
  },
  methods: {
    fetchPaperData() {
      if (this.curPage.status === 3) {
        return axios.all([
          axios.get('/pad/j/fetch/paper/questions', { params: { paperId: this.paperId } }),
          axios.get('/pad/j/coursematerial/report', { params: { courseMaterialId: this.curPage.cmId, subTargetKind: this.targetKind } }),
        ]).then(axios.spread((qResp, rResp) => {
          const questionsLookUp = {}
          const questionArr = []
          for (const questionData of qResp.data.questions) {
            const q = getQuestionByData(questionData)
            questionsLookUp[q.key] = q
            questionArr.push(q)
          }
          for (const reportData of rResp.data.report) {
            const key = `key-${reportData.kind}-${reportData.id}`
            if (questionsLookUp.hasOwnProperty(key)) {
              questionsLookUp[key].injectReportData(reportData)
            }
          }
          this.$store.commit('updateQuestions', { questions: questionArr })
          this.$store.commit('pageReportAttached')
        }))
      }
      return axios.get('/pad/j/fetch/paper/questions', { params: { paperId: this.paperId } })
        .then((response) => {
          const questions = []
          for (const questionData of response.data.questions) {
            questions.push(getQuestionByData(questionData))
          }
          this.$store.commit('updateQuestions', { questions })
        })
    },
  },
}
</script>

<style lang="less" scoped>
.question-container {
  font-family: Palatino, Times, Arial, SimSun, "Microsoft YaHei", "微软雅黑", STHeiti, sans-serif;
  line-height: 1.4;
  height: 100%;
  width: 100%;
}
</style>



