<template>
<div class="quesiton-slide-show" v-if="question">
  <div class="main-section">
    <span class="type-label">{{question.kindName}}</span>
    <span v-html="question.stemHtml" class="richtext"></span>
    <div v-for="(subQuestion, qIdx) in question.subQuestions" :key="subQuestion.id">
      <template v-if="question.subQuestions.length > 1">{{qIdx + 1}}. </template>
      <span v-html="subQuestion.stemHtml" ></span>
    </div>
    <div class="ui accordion" v-if="curPage.status === 3">
      <div class="title">
        <span class="accordion-title-label">作答详情<i class="dropdown icon"></i></span>
      </div>
      <div class="content">
        <inline-subjective-results :question="question"
          :all-students="allStudents"></inline-subjective-results>
      </div>
    </div>
    <div v-for="(subQuestion, qIdx) of question.subQuestions" :key="subQuestion.id">
      <div class="ui accordion">
        <div class="title">
          <span v-if="question.subQuestions.length > 1" class="accordion-title-label">小题 {{qIdx + 1}} / 解析<i class="dropdown icon"></i></span>
          <span v-else class="accordion-title-label">查看解析<i class="dropdown icon"></i></span>
        </div>
        <div class="content">
          <div v-for="answer of subQuestion.answers" :key="answer.id" v-html="answer.textHtml"></div>
          <div v-html="subQuestion.analysisHtml"></div>
        </div>
      </div>
    </div>
  </div>
</div>
</template>

<script>
import { mapState, mapGetters } from 'vuex'
import InlineSubjectiveResults from './inline-subjective-results'

require('assets/css/richtext.less')
require('./base.less')

export default {
  name: 'question-liberal-material',
  props: ['question'],
  data() {
    return {}
  },
  mounted() {
    $('.ui.accordion').accordion()
    this.$nextTick(() => {
      MathJax.Hub.Queue(['Typeset', MathJax.Hub])
    })
  },
  beforeDestroy() {
    $('.ui.accordion').accordion('destroy')
  },
  components: {
    'inline-subjective-results': InlineSubjectiveResults,
  },
  computed: {
    ...mapState(['curPage']),
    ...mapGetters(['allStudents']),
  },
}
</script>



