<template>
<div class="quesiton-slide-show" v-if="question">
  <div class="main-section">
    <span class="type-label">{{question.kindName}}</span>
    <span v-html="question.stemHtml" class="richtext"></span>
    <template v-for="(subQuestion, qIdx) in question.subQuestions">
      <div :key="subQuestion.id">
        <template v-if="question.subQuestions.length > 1">{{qIdx + 1}}. </template>
        <span v-html="subQuestion.stemHtml" ></span>
      </div>
    </template>
    <div class="ui accordion" v-if="curPage.status === 3">
      <div class="title">
        <span class="accordion-title-label">作答详情<i class="dropdown icon"></i></span>
      </div>
      <div class="content">
        <inline-subjective-results :question="question"
          :all-students="allStudents"></inline-subjective-results>
      </div>
    </div>
    <div v-for="(subQuestion, qIdx) of question.subQuestions" :key="subQuestion.id">
      <div class="ui accordion">
        <div v-for="(solution, idx) in subQuestion.solutions" class="title" :key="idx">
          <span v-if="question.subQuestions.length > 1" class="accordion-title-label">小题 {{qIdx + 1}} / 解析<i class="dropdown icon"></i></span>
          <span v-else class="accordion-title-label">查看解析<i class="dropdown icon"></i></span>
        </div>
        <div class="content" :key="idx">
          <div v-if="solution.answers.length">
            <span v-for="answer of solution.answers" :key="answer.id" v-html="answer.textHtml"></span>
          </div>
          <div v-if="solution.analyses.length">
            <span v-for="analysis of solution.analyses" :key="analysis.id" v-html="analysis.textHtml"></span>
          </div>
          <p v-if="solution.textHtml" v-html="solution.textHtml"></p>
          <div class="tag-list" v-if="solution.knowledges.length">
            <span class="item-title-label">考点</span>
            <span class="ui label" v-for="knowledge in solution.knowledges" :key="knowledge.id">{{knowledge.name}}</span>
          </div>
          <div class="tag-list" v-if="solution.methods.length">
            <span class="item-title-label">解题方法</span>
            <span class="ui label" v-for="method in solution.methods" :key="method.id">{{method.name}}</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</div>
</template>

<script>
import { mapState, mapGetters } from 'vuex'
import InlineSubjectiveResults from './inline-subjective-results'

require('assets/css/richtext.less')
require('./base.less')

export default {
  name: 'question-science-response',
  props: ['question'],
  data() {
    return {}
  },
  mounted() {
    $('.ui.accordion').accordion()
    this.$nextTick(() => {
      MathJax.Hub.Queue(['Typeset', MathJax.Hub])
    })
  },
  components: {
    'inline-subjective-results': InlineSubjectiveResults,
  },
  beforeDestroy() {
    $('.ui.accordion').accordion('destroy')
  },
  computed: {
    ...mapState(['curPage']),
    ...mapGetters(['allStudents']),
  },
}
</script>



