<template>
<div class="sub-question-panel" v-if="curSubQ">
  <div class="panel-header">
    <div class="sub-seq flex-1">
      小题：
      <template v-if="question.subQuestions.length > 1">
        <button v-for="(subQ, idx) of question.subQuestions" :key="subQ.id"
                :class="curSubQIdx === idx ? 'blue' : ''" class="ui circular button"
                @click="updateCurSubQIdx(idx, 'seq-btn')">
          {{idx + 1}}
        </button>
      </template>
    </div>
    <div class="action-bar">
      <div class="ui analysis button">解析</div>
      <div class="ui analysis popup transition hidden">
        <span class="ui header">正确答案：</span>
        <span>{{curSubQ.answerLabel}}</span>
        <h5 class="ui header">解析：</h5>
        <div v-html="curSubQ.analysisHtml"></div>
        <template v-if="curSubQ.knowledges.length">
          <h5 class="ui header">考点</h5>
          <span class="ui label" v-for="knowledge in curSubQ.knowledges" :key="knowledge.id">{{knowledge.name}}</span>
        </template>
      </div>
      <div class="ui icon buttons">
        <button class="ui button" :class="subPanelMin? 'disabled':''" @click="$store.commit('toggleSubPane')">
          <i class="window minimize icon"></i>
        </button>
        <button class="ui button" :class="subPanelMin? '':'disabled'" @click="$store.commit('toggleSubPane')">
          <i class="window maximize icon"></i>
        </button>
      </div>
    </div>
  </div>
  <div class="sub-question" v-show="!subPanelMin">
    <div class="sub-qbody flex-1">
      <div v-if="hasSubStem" v-html="curSubQ.stemHtml" class="richtext"></div>
      <div class="options">
        <div class="option" v-for="option in curSubQ.options" :class="option.isAnswer && curPage.status === 3 ?'option-correct':''" :key="option.id">
          <span class="seq-text">{{option.seqLabel}}. </span>
          <span v-html="option.optionHtml"></span>
        </div>
      </div>
    </div>
    <div class="q-stat" v-if="curPage.hasReport">
      <div class="stat-chart">
        <bar-chart class="normal content"
          :options="curSubQ.options"
          :totalStuCount="totalStuCount"
          v-if="curSubQ.hasReport"></bar-chart>
        <div class="overlay content"></div>
        <button class="ui primary button action content" @click="showStat=true">查看详情</button>
      </div>
      <modal-objective-results
        :question="curSubQ"
        :allStudents="allStudents"
        @close="showStat=false"
        v-if="showStat && curSubQ && curSubQ.hasReport"
        ></modal-objective-results>
    </div>
  </div>
</div>
</template>


<script>
import { mapState, mapGetters } from 'vuex'
import BarChart from 'components/questions/bar-chart'
import ModalObjectiveResults from 'components/questions/modal-objective-results'

require('assets/css/richtext.less')

export default {
  name: 'sub-question-panel',
  props: {
    question: {
      type: Object,
      required: true,
    },
    hasSubStem: {
      type: Boolean,
      default: true,
    },
  },
  data() {
    return {
      curSubQIdx: 0,
      showStat: false,
    }
  },
  components: { BarChart, ModalObjectiveResults },
  computed: {
    curSubQ() {
      if (this.question.subQuestions.length > 0) {
        return this.question.subQuestions[this.curSubQIdx]
      }
      return null
    },
    ...mapState(['subPanelMin', 'curPage']),
    ...mapGetters(['allStudents', 'totalStuCount']),
  },
  mounted() {
    $('.analysis.button').popup({
      popup: $('.analysis.popup'),
      on: 'click',
      position: 'top right',
    })
    window.addEventListener('keyup', this.changeSubIdx)
  },
  beforeDestroy() {
    $('.analysis.button').popup('destroy')
    window.removeEventListener('keyup', this.changeSubIdx)
  },
  methods: {
    changeSubIdx(e) {
      const length = this.question.subQuestions.length
      if (!e.ctrlKey && e.keyCode === 37 && this.curSubQIdx > 0) {
        this.curSubQIdx -= 1
      } else if (!e.ctrlKey && e.keyCode === 39 && this.curSubQIdx < length - 1) {
        this.curSubQIdx += 1
      }
    },
    updateCurSubQIdx(val, from) {
      this.curSubQIdx = val
      if (from === 'seq-btn' && this.subPanelMin) {
        this.$store.commit('toggleSubPane')
      }
    },
  },
}
</script>

<style lang="less" scoped>
@import '~assets/css/base-variables.less';
.flex-1 {
  flex: 1;
}
.sub-question-panel {
  .sub-question {
    display: flex;
  }
  .panel-header {
    display: flex;
    .action-bar {
      text-align: right;
      width: 50rem;
    }
  }
}

.sub-question-panel, .ui.popup {
  background-color: #fff;
  border-top: 2px solid @color-primary;
}

.sub-question-panel {
  margin-left: 10px;
  margin-right: 10px;
  border-top: 2px solid @color-primary;
  padding: 4px 12px;
  z-index: 1;
  box-shadow: 0 1px 6px rgba(0,0,0,.4), 0 -1px 2px 1px rgba(0,0,0,.2);
  max-height: 60vh;

  .sub-question {
    border-top: #ddd 1px solid;
    margin-top: 3px;
    padding-top: 10px;
    overflow-y: auto;

  }
  .sub-seq {
    .ui.button {
      box-sizing: border-box;
      font-weight: normal;
      padding: 0;
      width: 28px;
      line-height: 28px;
      font-size: 14px;
      border-radius: 14px;
    }
  }
  .sub-seq, .action-bar {
    font-size: initial;
    font-family: "Helvetica Neue",Helvetica,"PingFang SC","Hiragino Sans GB","Microsoft YaHei","微软雅黑",Arial,sans-serif;
  }

  .options {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    color: #404040;
    .option {
      flex-grow: 1;
      margin-right: 1.5rem;
      min-width: 0;
    }
    .seq-text {
      color: #000;
      font-weight: bold;
      display: inline-block;
      width: 1em;
      text-align: right;
    }
    .option-correct {
      color: @color-teal;
      .seq-text {
        color: @color-teal;
      }
    }
  }
  .analysis.popup {
    text-align: left;
    font-size: inherit;
    max-width: 50%;
  }
}
</style>



