<template>
<div class="sub-question-panel" v-if="curSubQ">
  <div class="panel-header">
    <div class="sub-seq flex-1">
      小题：
      <template v-if="question.subQuestions.length > 1">
        <button v-for="(subQ, idx) of question.subQuestions" :key="subQ.id"
                :class="curSubQIdx === idx ? 'blue' : ''" class="ui circular button"
                @click="updateCurSubQIdx(idx, 'seq-btn')">
          {{idx + 1}}
        </button>
      </template>
    </div>
    <div class="action-bar">
      <div class="ui icon buttons">
        <button class="ui button" :class="subPanelMin? 'disabled':''" @click="$store.commit('toggleSubPane')">
          <i class="window minimize icon"></i>
        </button>
        <button class="ui button" :class="subPanelMin? '':'disabled'" @click="$store.commit('toggleSubPane')">
          <i class="window maximize icon"></i>
        </button>
      </div>
    </div>
  </div>
  <div class="sub-question" v-show="!subPanelMin">
    <div class="sub-qbody flex-1">
      <div v-if="hasSubStem" v-html="curSubQ.stemHtml" class="richtext"></div>
      <div>
        <span class="label">正确答案：</span>
        <span v-for="answer of curSubQ.answers" :key="answer.id">{{answer.text}}</span>
      </div>
      <span class="label">解析：</span>
      <span v-html="curSubQ.analysisHtml"></span>
    </div>
    <div class="q-stat" v-if="curPage.hasReport">
      <div class="stat-chart">
        <bar-chart
          :blankQuestion="curSubQ"
          :totalStuCount="totalStuCount"
          v-if="curSubQ.hasReport"></bar-chart>
        <div class="overlay content"></div>
        <button class="ui primary button action content" @click="showStat=true">查看详情</button>
      </div>
      <modal-blank-results
        :question="curSubQ"
        :allStudents="allStudents"
        @close="showStat=false"
        v-if="showStat"></modal-blank-results>
    </div>
  </div>
</div>
</template>


<script>
import { mapState, mapGetters } from 'vuex'
import BarChart from 'components/questions/bar-chart'
import ModalBlankResults from 'components/questions/modal-blank-results'

require('assets/css/richtext.less')

export default {
  name: 'blank-sub-panel',
  props: {
    question: {
      type: Object,
      required: true,
    },
    hasSubStem: {
      type: Boolean,
      default: true,
    },
  },
  data() {
    return {
      curSubQIdx: 0,
      showStat: false,
    }
  },
  components: { BarChart, ModalBlankResults },
  computed: {
    curSubQ() {
      if (this.question.subQuestions.length > 0) {
        return this.question.subQuestions[this.curSubQIdx]
      }
      return null
    },
    ...mapState(['subPanelMin', 'curPage']),
    ...mapGetters(['allStudents', 'totalStuCount']),
  },
  mounted() {
    window.addEventListener('keyup', this.changeSubIdx)
  },
  beforeDestroy() {
    window.removeEventListener('keyup', this.changeSubIdx)
  },
  methods: {
    changeSubIdx(e) {
      const length = this.question.subQuestions.length
      if (!e.ctrlKey && e.keyCode === 37 && this.curSubQIdx > 0) {
        this.curSubQIdx -= 1
      } else if (!e.ctrlKey && e.keyCode === 39 && this.curSubQIdx < length - 1) {
        this.curSubQIdx += 1
      }
    },
    updateCurSubQIdx(val, from) {
      this.curSubQIdx = val
      if (from === 'seq-btn' && this.subPanelMin) {
        this.$store.commit('toggleSubPane')
      }
    },
  },
}
</script>

<style lang="less" scoped>
@import '~assets/css/base-variables.less';
.flex-1 {
  flex: 1;
}

.sub-question-panel {
  .sub-question {
    display: flex;
  }
  .panel-header {
    display: flex;
    .action-bar {
      text-align: right;
      width: 50rem;
    }
  }

  margin-left: 10px;
  margin-right: 10px;
  border-top: 2px solid @color-primary;
  padding: 4px 12px;
  z-index: 1;
  box-shadow: 0 1px 6px rgba(0,0,0,.4), 0 -1px 2px 1px rgba(0,0,0,.2);
  max-height: 60vh;

  .sub-question {
    border-top: #ddd 1px solid;
    margin-top: 3px;
    padding-top: 10px;
    overflow-y: auto;
  }
  .sub-seq {
    .ui.button {
      box-sizing: border-box;
      font-weight: normal;
      padding: 0;
      width: 28px;
      line-height: 28px;
      font-size: 14px;
      border-radius: 14px;
    }
  }
  .sub-seq, .action-bar {
    font-size: initial;
    font-family: "Helvetica Neue",Helvetica,"PingFang SC","Hiragino Sans GB","Microsoft YaHei","微软雅黑",Arial,sans-serif;
  }
}
</style>



